.info-container {
    line-height: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1 0 auto;
}
.info-container h2 {
    margin-bottom: 20px;
    margin-top: 20px;
}

@media screen and (max-width: 650px) {
    .info-container {
        font-size: .8em;
    }
}